@import "variables";
:root {
  font-size: 16px;
}
/* Base Styling  */
body {
  background-color: $bg-color;
  color: $text-color;
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  font-family: "Open Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  overflow-y: scroll;
}

/* Layout Styling  */
.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  font-size: 1.25rem;
  min-height: 80vh;
  > * {
    width: calc(min(80vw, 45rem));
  }
}

.info-box {
  background-color: $bg-color2;
  padding: 1.5rem;
  border-radius: 2rem;

  transition: transform ease 0.5s;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    &:first-child {
      margin-top: 0;
    }
  }

  &:hover {
    transform: scale(1.03);
    transform-origin: 50%, 50%;
  }
}

/** Content Styling **/

a {
  color: $text-color;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;

  transition: text-decoration ease 0.2s;

  text-decoration: underline solid;
  text-decoration-color: rgba(255, 255, 255, 0.7);
  text-decoration-thickness: 2px;

  &:hover {
    text-decoration-color: white;
  }
}
.link-button {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 1rem;
  padding: 0.5rem;
  > * {
    margin: 0.25rem;
  }

  border-radius: 20px;
  border: 1px solid $bg-color2;
  background-color: $bg-color2;
  transition: 0.2s;
  text-decoration: none;

  &:hover {
    background-color: $bg-color;
    border: 5px solid $bg-color2;
  }
}

.center {
  text-align: center;
}

.image-caption {
  color: $light-font-color;
  font-size: 0.8em;
  width: 100%;
}

h1,
h2,
h3 {
  font-family: Montserrat;
  margin-left: 0;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

hr {
  width: auto;
  height: 0;
  border: 0.2rem solid $bg-color2;
  border-radius: 15px;
  margin-bottom: 1.5rem;
}
.info-box > hr {
  border-color: $bg-color;
}

ul {
  padding-left: 1.5rem;
  > li {
    padding-bottom: 0.25rem;
  }
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
svg {
  height: 1em;
}

/* Fade In Animation */

.fade-in {
  opacity: 0;
  transform: translateY(1rem);
  animation: fadeIn 0.7s ease-in-out 0.2s forwards;
}
.fade-in--no-move {
  opacity: 0;
  animation: fadeInNoMove 0.4s ease-in-out 0.1s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(1rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInNoMove {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
